body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*,
::before,
::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.bg-body {
  background-color: #000000;
}

.tabs-container {
  display: flex;
  flex-direction: column;
  position: relative;
  width: 90%;
  height: 820px;
  background: #f1f1f1;
  margin: 50px auto 0;
  word-break: break-word;
  border: 1px solid rgba(0, 0, 0, 0.274);
  border-radius: 10px;
  box-shadow: 5px 5px 10px #888888;
}

.bloc-tabs {
  display: flex;
}

.tabs {

  padding: 15px;
  text-align: center;
  width: 50%;
  background: rgba(128, 128, 128, 0.075);
  cursor: pointer;
  border-bottom: 1px solid rgba(0, 0, 0, 0.274);
  box-sizing: content-box;
  position: relative;
  outline: none;
}

.tabs:not(:last-child) {
  border-right: 1px solid rgba(0, 0, 0, 0.274);
}

.active-tabs {
  background: white;
  border-bottom: 1px solid transparent
}

.active-tabs::before {
  content: "";
  display: block;
  position: absolute;
  top: -5px;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  width: calc(100% + 2px);
  height: 5px;
  background: rgb(88, 147, 241);
}

button {
  border: none;

}

.content-tabs {
  flex-grow: 1;
}

.content {
  background: white;
  padding: 20px;
  width: 100%;
  height: 100%;
  display: none;
}

.content h2 {
  padding: 0px 0 5px 0px;
}

.content hr {
  width: 100px;
  height: 2px;
  background: #222;
  margin-bottom: 5px;
}

.content p {
  width: 100%;
  height: 100%;
}

.active-content {
  display: block;
}
.active-queue-item {
    width: 5rem;
    text-align: center;
}

.queue-table {
    /* position: relative; */
    /* display: flex; */
    /* justify-content: center; */
    margin: auto auto;
    /* align-items: center; */
}

.passive-queue-item {
    width: 5rem;
    color:rgba(67, 72, 73, 0.911);
    text-align: center;
}

.queue-index {
    all: unset;
}

.item-container {
    width: 100%;
    height: 75px;
    display: flex;
    justify-content: right;
}

h2 {
    margin: 10px;
    text-align: center;
}

.trans {
    color: transparent;
}
.canvas {
    border-radius: 5px;
    height: 300px;
